.root {
  max-height: 100%;
  max-width: 100%;
  text-align: center;
}

.containerText {
  width: 100%;
  position: relative;
  text-align: center;
  color: black;
}

.bannerContainer {
  width: 100%;
}

.bannerDiv {
  width: 100%;
  opacity: 0.5;
}

.caption {
  font-size: 24px;
}

.caption2 {
  font-size: 24px;
  text-align: justify;
  justify-content: center;
  line-height: 140%;
}

.headerContainer {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.quoteContainer {
  display: flex;
  justify-content: space-around;
  padding: 5%;
}

.LineChartContainer {
